      <div style="border: 3px solid #CCCCCC; width: 250px"></div>
      <div style="width: 100%; margin: auto">
        <p >
          <b>· 2024-12: </b>
        </p>
        <p>
          New Paper! Our database CancerSRT, a spatially resolved transcriptomics database for human cancers was published at Journal of Genetics and Genomics!
        </p>

        <p >
          <b>· 2024-09: </b>
        </p>
        <p >
          We are looking for creative and highly motivated students to join our group.
          If you want to discuss potential opportunities, contact us.
        </p>
        <p >
          <b>· 2024-09: </b>
        </p>
        <p >
          The lab welcomes new students, Jianxing Zhang, Zhuoxin Yao, Zhipeng Tan and Rui Zhao.
        </p>
        <p>
        <p v-if="!showMore">
          <a href="#" @click.prevent="toggleShowMore">More News</a>
        </p>
        <div v-if="showMore">
        <p>
          <b>· 2024-06: </b>
        </p>
        <p>
          Congratulations to Yuying Huo on being awarded the Excellent Master thesis of Beijing Jiaotong University.
        </p>
        <p>
          <b>· 2024-06: </b>
        </p>
        <p>
          Congratulations to Yuying Huo, Jiakang Wang and Mengnan Gao on their graduation with master's degrees.
        </p>
        <!-- 收起按钮 -->
        <p>
          <a href="#" @click.prevent="toggleShowMore">Show Less</a>
        </p>
      </div>
        </p>
      </div>
    </div>
    <div style="margin-bottom: 50px;">
      <JoinUs />
    </div>

  </div>
</template>

<script>
import JoinUs from './JoinUs.vue'
import Resume from './Resume'
import Publications from './Publications'
import Softwares from './Softwares'
import Members from './Teacher'
import Contact from './Contact'
import Memory from './Memory'

export default {
  name: 'Home',
  components: { Memory, Contact, Members, Softwares, Publications, Resume, JoinUs },
  data () {
    return {
      showMore: false, // 控制展开和收起的状态
      start_div: true
    }
  },
  mounted () {
    this.start_div = false
  },
  methods: {
    toggleShowMore() {
      this.showMore = !this.showMore; // 切换状态
    },
  }
}
