}

h4 {
  text-align: left;
  color: white;
  background-color: #005BAC;
  width: 100%;
  margin-bottom: 5px;
  margin-top: 5px;
  text-indent: 16px;
}

.titles {
  color: #005BAC;
  text-align: left;
  font-size: 28px;
  margin: 16px 0 0 0;
  padding: 0;
}

.container{
  margin: auto; 
  width: 1000px;
}

/* 框的样式 */
.memory-box {
  border-radius: 0px;
  margin-top: 20px;
  flex-direction: column;
  justify-content: flex-start;
  border: 2px solid #005BAC;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  margin-bottom: 10px;
}

.top-bar {
  font-size: 26px;
  padding: 0px;
  width: 98.5%;
  background-color: #005BAC;
  color: white;
  text-align: left;
  margin-bottom: 10px;
}

p{
  font-size: 20px;
  text-align: left; 
  margin: 0px; 
  margin-left: 16px; 
  font-family: Arial;
  word-wrap: break-word; /* 自动换行 */
  word-break: break-word; /* 强制换行 */
  max-width: 100%; /* 保证不会超出屏幕宽度 */
