  }

  .top-bar {
    font-size: 22px; /* 手机设备顶部字体大小 */
  }

  .memory-box {
 
  }

  .top-bar {
    
  }

  /* 调整新闻内容字体大小 */
  .news-text {
    font-size: 14px;
  }
  p{
  font-size: 16px;
  text-align: left; 
  margin: 0px; 
  margin-left: 16px; 
  font-family: Arial;
  word-wrap: break-word; /* 自动换行 */
  word-break: break-word; /* 强制换行 */
  max-width: 100%; /* 保证不会超出屏幕宽度 */
  white-space: normal; /* 允许文字换行 */
  }
  b{
  font-size: 22px; 
  font-family: Arial
}
}


@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}
</style>



