<script>
export default {
  data () {
    return {
      isMobile: false,
    }
  },
  name: 'People',
  methods: {
    checkScreenSize() {
      // 判断屏幕宽度是否小于768px
      this.isMobile = window.innerWidth < 768;
    },
    goTeacherPage () {
      this.$router.push({ path: '/Teacher' })
    }
  },
  mounted() {
    // 初始化屏幕宽度检测
    this.checkScreenSize();
    // 监听窗口大小变化
    window.addEventListener("resize", this.checkScreenSize);
  },
  beforeDestroy() {
    // 移除事件监听器
    window.removeEventListener("resize", this.checkScreenSize);
  },
}
</script>

