<!--          <span style="text-align: left; font-family: 'Arial';font-size: 36px;color: #005BAC;">-->
<!--              Peking University Bioinformatics: Learning methods-->
<!--          </span>-->
          <span class="title-span">Peking University Bioinformatics: Learning methods</span>
        </p>
        <p style="text-indent: 2em">
          <span style="text-align: center;font-family: 'Arial';">
            As a cutting-edge interdisciplinary course, this course is mainly aimed at senior undergraduates and first-year postgraduates majoring in bioinformatics, molecular biology, genetics, computer science, mathematics, statistics, physics and other related majors. Students should have basic knowledge of molecular biology and basic computational science knowledge and skills (including elementary probability theory, linear algebra, a programming language), and have an understanding of basic terminology in related fields (such as genes, expression, regulation, conditional probability, expectations, Bayes formula, etc.). Click <a href="https://www.bilibili.com/video/BV13t411G7oh/?spm_id_from=333.337.search-card.all.click&vd_source=18d740de3fbabf12f2b78d8eb8680b96" target="_blank">here</a> to visit.
          </span>
        </p>
