    <div class="card">
      <div class="card-left">
        <el-image
          style="width: 100%; object-fit:cover;max-height: 350px;object-position: center top;"
          :src="url_seurat">
        </el-image>
        <!--        <img src="../assets/cancersrt.png" alt="" width="100%" style="">-->
      </div>
      <div class="card-right">
        <p>
          <span class="title-span">Seurat</span>
        </p>
        <p style="text-indent: 2em">
          <span style="text-align: center;font-family: 'Arial';">
            Seurat is an R package designed for QC, analysis, and exploration of single-cell RNA-seq data. Seurat aims to enable users to identify and interpret sources of heterogeneity from single-cell transcriptomic measurements, and to integrate diverse types of single-cell data. Click <a href="https://satijalab.org/seurat/" target="_blank">here</a> to visit.
          </span>
        </p>
      </div>
    </div>
    <div class="card">
      <div class="card-left">
