  text-align: left;
  margin: 20px 0 0 0;
  padding: 0;
}
.card{
  display: flex;
  justify-content: space-evenly;
  margin: 50px 0 50px 0;
}
.card-left{
  width: 50%;
}
.card-right{
  flex: 1;
  margin-left: 5%;
}
p{
  font-size: 20px;
  line-height: 30px;
  text-align: justify;
}
.title-span{
  font-size: 36px;
  font-family: "Arial";
  color: #005BAC;
}
</style>



